@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish:wght@200&display=swap');

.chat-app {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Mulish';
    text-decoration: none;
    width: 100%;
    height: auto;
    margin: 0 auto;
    border-radius: 5px;
    overflow: hidden;
    background-color: #008000;
  }
  .header {
    background-color: #008000;
    font-family: 'Mulish';
    color: white;
    border-radius: 5px;
    width: 100%;
    text-align: center;
  }
  .messages {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 80%;
    font-size: 15px;
    overflow-y: auto;
    padding: 30px;
    padding-right: 30px;
    margin-bottom: 10px;
    font-family: 'Mulish';
  }
  .user {
    font-weight: bold;
    margin-right: 10px;
    color: #333;
    
  }
  
  .new-message-form {
    display: flex;
    width: 100%;
    
  }
  
  .new-message-input {
    flex: 1;
    border: none;
    outline: none;
    background-color: #fff;
    font-size: 16px;
    color: #333;
    padding: 10px;
    border-radius: 8px;
  }
  
  .send-button {
    border-radius: 8px;
    background-color: #008000;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    font-family: 'Mulish';
  }
  .attach {
    border-radius: 8px;
    background-color: #008000;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    font-family: 'Mulish';
  }