@import url('https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish:wght@200&display=swap');

body {
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  font-size: 24px;
  flex-direction: column;
  font-family: 'Mulish';
}

.app-header {
  text-align: center;
  background-color: #FAF9F4;
  font-family: 'Mulish';
  font-size: 24px;
  color: #141E0D;
  width: 100%;
  
}

.app-container {
  margin-top: 50px;
  background-color: #FAF9F4;
  
}

.room {
  display: flex;
  flex-direction: column;
}

.room label {
  text-align: center;
  font-size: 25px;
  margin-bottom: 20px;
}

.room input {
  width: 300px;
  height: 30px;
  border: 2px solid #008000;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
}
.room button {
  width: 210px;
  height: 40px;
  border: none;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-align: center;
  margin: 5px;
  background-color: #008000;
  color: white;
  cursor: pointer;
}

.sign-out button {
  margin-top: 50px;
  width: 210px;
  height: 40px;
  border: none;
  border-radius: 6px;
  padding-left: 5px;
  font-size: 20px;
  text-decoration: none;
  text-align: center;
  background-color: #008000;
  color: white;
  cursor: pointer;
}

